import { HttpClient } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { forkJoin, Observable } from 'rxjs'
import { map, tap } from 'rxjs/operators'
import { SessionService } from 'src/app/common/services/session.service'
import { IFee } from '../../common/api-interfaces'
import { FeesStore } from './fees.store'

@Injectable({ providedIn: 'root' })
export class FeesService {
    constructor(protected store: FeesStore, private http: HttpClient, private session: SessionService) {}

    public fetchAsObservable(): Observable<IFee[]> {
        return forkJoin([
            this.http.get<IFee[]>(`/users/${this.session.user.id}/fee-schedule`, {
                params: {
                    currencyCode: 'AU',
                },
            }),
            this.http.get<IFee[]>(`/users/${this.session.user.id}/fee-schedule`, {
                params: {
                    currencyCode: 'AG',
                },
            }),
            this.http.get<IFee[]>(`/users/${this.session.user.id}/fee-schedule`, {
                params: {
                    currencyCode: 'USD',
                },
            }),
        ]).pipe(
            map(([goldFees, silverFees, usdFees]) => [...goldFees, ...silverFees, ...usdFees]),
            tap(fees => {
                this.store.set(fees)
            })
        )
    }

    public fetch(): void {
        this.fetchAsObservable().subscribe(() => undefined)
    }
}
