import { Component, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { Observable, of, Subscription } from 'rxjs'
import { environment } from 'src/environments/environment'
import { FeeType, IFee, Transaction, TransactionMethod } from '../common/api-interfaces'
import { SessionService } from '../common/services/session.service'
import { FeesQuery } from '../store/fees/fees.query'
import { FeesService } from '../store/fees/fees.service'

@Component({
    selector: 'transfer',
    templateUrl: 'transfer.component.html',
})
export class TransferComponent implements OnInit, OnDestroy {
    public appName = environment.appName
    public transaction: Transaction
    public fixedRecipient = false
    private subscriptions = new Subscription()

    constructor(
        private route: ActivatedRoute,
        public session: SessionService,
        private feesQuery: FeesQuery,
        private feesService: FeesService
    ) {}

    public ngOnInit(): void {
        if (this.route.snapshot.queryParamMap.has('method')) {
            this.resetTransaction(this.route.snapshot.queryParamMap.get('method') as TransactionMethod)
        }
        this.route.queryParamMap.subscribe(params => {
            this.resetTransaction((params.get('method') as TransactionMethod) || 'send')
        })
        if (environment.appName === 'sgpmx') {
            this.feesService.fetch()
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public getFee(type: FeeType): Observable<IFee | null> {
        if (!this.transaction.currency) {
            return of(null)
        }
        return this.feesQuery.findByPrimaryAttributes({
            type,
            method: 'internal',
            currencyCode: this.transaction.currency.code,
        })
    }

    private resetTransaction(method: TransactionMethod | 'request' | 'send'): void {
        switch (method) {
            default:
                this.transaction = {
                    type: 'transfer',
                    method: 'internal',
                } as Transaction
                break
        }
    }
}
