import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { faQuestionCircle } from '@fortawesome/pro-light-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { cloneDeep } from 'lodash'
import { combineLatest, forkJoin, Observable, of, Subject, Subscription, timer } from 'rxjs'
import { flatMap, map, switchMap, take } from 'rxjs/operators'
import { CurrenciesService } from 'src/app/store/currencies/currencies.service'
import { environment } from 'src/environments/environment'
import { IContact, ICurrency, Paginated } from '../../common/api-interfaces'
import { ContactBookComponent, openContactBook } from '../../common/contact-book/contact-book.component'
import { Currency } from '../../common/models/accounting/currency.model'
import { Transaction } from '../../common/models/accounting/transaction.model'
import { Wallet } from '../../common/models/accounting/wallet.model'
import { PaymentConfirmationComponent } from '../../common/payment/payment-confirmation/payment-confirmation.component'
import { ConfirmationService } from '../../common/services/confirmation.service'
import { SessionService } from '../../common/services/session.service'
import { TransactionViewComponent } from '../../common/transaction/transaction-view/transaction-view.component'
import { CurrenciesQuery } from '../../store/currencies/currencies.query'
import { WalletsService } from '../../store/wallets/wallets.service'

@Component({
    selector: 'sgpmx-transfer-form',
    templateUrl: 'sgpmx-transfer-form.component.html',
})
export class SgpmxTransferFormComponent implements OnInit, OnDestroy {
    @Input()
    public transaction: Partial<Transaction>
    public appName = environment.appName
    public displayName = environment.displayName

    public recipient: string
    public isUploadCollapsed = true
    public fetchEvent = new Subject<void>()
    public original: Partial<Transaction>
    public files: File[] = []
    public faQuestionCircle = faQuestionCircle

    public maxUsableAmount: string | null

    @ViewChild('transferForm')
    public transferForm: NgForm
    @Output()
    public readonly transactionChange = new EventEmitter<Partial<Transaction>>()
    @Output()
    public readonly transactionComplete = new EventEmitter<void>()

    public isDefineByValue = true
    public valueInUsd: string | null = null

    private wallets: Wallet[]
    private subscriptions = new Subscription()

    constructor(
        public session: SessionService,
        private currenciesQuery: CurrenciesQuery,
        private http: HttpClient,
        private ngbModal: NgbModal,
        private confirmation: ConfirmationService,
        private walletsService: WalletsService,
        private currenciesService: CurrenciesService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.original = cloneDeep(this.transaction)
        this.subscriptions.add(
            timer(1, 16 * 1000)
                .pipe(
                    switchMap(() => this.http.post<void>('/sgpmx/refresh-exchange-rates', {})),
                    flatMap(() => this.currenciesService.fetchAsObservable())
                )
                .subscribe(() => {
                    this.transaction.currency = this.currenciesQuery.getEntity(this.transaction.currency?.code!)
                    this.calculateAmount()
                })
        )
        this.subscriptions.add(
            combineLatest(this.fetchEvent, this.session.userStream)
                .pipe(
                    switchMap(([_, user]) =>
                        forkJoin([
                            of(user),
                            this.http.get<Wallet[]>(`/users/${user.id}/wallets`),
                            this.http
                                .get<Paginated<ICurrency>>('/currencies', {
                                    params: {
                                        'filter.transferable': JSON.stringify(true),
                                    },
                                })
                                .pipe(map(response => response.data)),
                        ])
                    )
                )
                .subscribe(([user, wallets, currencies]) => {
                    if (!this.transaction.currency) {
                        this.transaction.currency = currencies[0]
                    }
                    this.transaction.baseWallet = {
                        user: {
                            id: user.id,
                            accountNumber: user.accountNumber,
                            name: user.name,
                        },
                    } as Wallet
                    this.wallets = wallets.filter(wallet => new BigNumber(wallet.balance).isGreaterThan(0))
                    this.transaction = Object.assign(this.transaction, {
                        fixedFee: '0',
                        relativeFee: '0',
                    })
                })
        )
        this.fetchEvent.next()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.transferForm && this.transferForm.invalid) {
            return
        }
        const modal = this.ngbModal.open(PaymentConfirmationComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
        })
        const component = modal.componentInstance as PaymentConfirmationComponent
        component.transaction = this.transaction as Transaction
        component.onConfirm.subscribe(() => {
            this.createTransaction().subscribe(
                transaction => {
                    component.isCompleted = true
                    modal.close()
                    const transactionView = this.ngbModal.open(TransactionViewComponent, {
                        windowClass: 'modal-primary',
                        backdrop: 'static',
                    })
                    const transactionViewModal = transactionView.componentInstance as TransactionViewComponent
                    transactionViewModal.transaction = transaction
                    this.reset()
                    this.walletsService.fetch()
                    this.fetchEvent.next()
                    this.transactionComplete.emit()
                },
                error => {
                    if (error.name === 'RecipientNotFoundError') {
                        modal.close()
                        this.confirmation
                            .show({
                                title: 'Recipient not found',
                                type: 'danger',
                                text: `The recipient has not been found. Please check if the account number or email address entered is correct.`,
                                confirmText: 'Got it',
                                showCancel: false,
                            })
                            .subscribe()
                    }
                    component.isSubmitted = false
                    component.reset()
                }
            )
        })
    }

    public openContactBook(): void {
        if (!this.transaction.baseWallet?.user) {
            return
        }
        const modal = openContactBook(this.ngbModal, this.transaction.baseWallet.user)
        this.subscriptions.add(
            (modal.componentInstance as ContactBookComponent).onSelect.subscribe((contact: IContact) => {
                this.recipient = contact.counterUser?.accountNumber
                this.onRecipientChange(contact)
                modal.close()
            })
        )
    }

    public onRecipientChange(contact?: IContact): void {
        this.transaction.counterWallet = {
            user: contact
                ? contact.counterUser
                : { accountNumber: !this.recipient ? '' : this.recipient.trim().replace(/\s/g, '') },
        } as Wallet
    }

    public useMaxAmount(): void {
        if (this.transaction.currency) {
            const target = this.wallets.find(wallet => wallet.currency.code === this.transaction.currency!.code)
            if (target) {
                const maxUsable = this.maxUsableAmount
                const balance = maxUsable
                    ? BigNumber.min(target.availableBalance, maxUsable).toString()
                    : target.availableBalance
                this.transaction.amount = new BigNumber(balance).toFixed(this.transaction.currency!.decimalPlaces)
            }
        }
        if (this.isDefineByValue) {
            this.calculateValueInUsd()
        }
    }

    public calculateValueInUsd(): void {
        this.currenciesQuery
            .selectEntity('USD')
            .pipe(take(1))
            .subscribe(usd => {
                if (!usd) {
                    return
                }
                if (!this.transaction.amount) {
                    this.valueInUsd = null
                    return
                }
                this.valueInUsd = new BigNumber(
                    Currency.convert(this.transaction.amount, this.transaction.currency!, usd)
                ).toFixed(2)
            })
    }

    public calculateAmount(): void {
        this.currenciesQuery
            .selectEntity('USD')
            .pipe(take(1))
            .subscribe(usd => {
                if (!usd) {
                    return
                }
                if (!this.valueInUsd) {
                    this.transaction.amount = undefined
                    return
                }
                this.transaction.amount = new BigNumber(
                    Currency.convert(this.valueInUsd, usd, this.transaction.currency!)
                ).toFixed(7)
            })
    }

    public onCurrencyChange(): void {
        this.fetchEvent.next()
        this.calculateAmount()
        this.calculateValueInUsd()
    }

    public getMaximum(): string | null {
        if (this.isDefineByValue || !this.transaction.currency) {
            return '100000'
        }
        return new BigNumber(100000)
            .times(this.transaction.currency.exchangeRate)
            .toFixed(this.transaction.currency.decimalPlaces)
    }

    public getMinimum(): string {
        if (this.isDefineByValue || !this.transaction.currency) {
            return '1'
        }
        return new BigNumber(1)
            .times(this.transaction.currency.exchangeRate)
            .toFixed(this.transaction.currency.decimalPlaces)
    }

    private createTransaction(): Observable<Transaction> {
        return this.http.post<Transaction>('/transactions', this.transaction).pipe(
            flatMap(transaction =>
                this.files.length > 0
                    ? forkJoin(
                          this.files.map(file =>
                              this.http.post<File>(`/transactions/${transaction.id}/files`, file, {
                                  headers: new HttpHeaders({
                                      'X-File-Name': encodeURIComponent(file.name),
                                      'Content-Type': file.type,
                                  }),
                              })
                          )
                      ).pipe(map(() => transaction))
                    : of(transaction)
            )
        )
    }

    private reset(): void {
        this.transaction = Object.assign(this.original, { currency: this.transaction.currency })
        this.transactionChange.emit(this.transaction)
        this.files = []
        this.transferForm.resetForm({
            'currency-select': this.transaction.currency,
        })
        this.isDefineByValue = true
        this.fetchEvent.next()
    }
}
