import { HttpClient, HttpHeaders } from '@angular/common/http'
import { AfterContentChecked, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ActivatedRoute } from '@angular/router'
import { faQuestionCircle } from '@fortawesome/pro-light-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { combineLatest, forkJoin, Observable, of, Subject, Subscription } from 'rxjs'
import { flatMap, map, switchMap, take, tap } from 'rxjs/operators'
import { CurrenciesQuery } from 'src/app/store/currencies/currencies.query'
import { WalletsQuery } from 'src/app/store/wallets/wallets.query'
import { environment } from 'src/environments/environment'
import { IContact, ICurrency, IFee, IUser } from '../../common/api-interfaces'
import { ContactBookComponent, openContactBook } from '../../common/contact-book/contact-book.component'
import { Currency } from '../../common/models/accounting/currency.model'
import { Transaction } from '../../common/models/accounting/transaction.model'
import { Wallet } from '../../common/models/accounting/wallet.model'
import { PaymentConfirmationComponent } from '../../common/payment/payment-confirmation/payment-confirmation.component'
import { FeeService } from '../../common/services/accounting/fee.service'
import { ConfirmationService } from '../../common/services/confirmation.service'
import { SessionService } from '../../common/services/session.service'
import { TransactionViewComponent } from '../../common/transaction/transaction-view/transaction-view.component'
import { WalletsService } from '../../store/wallets/wallets.service'

@Component({
    selector: 'transfer-form',
    templateUrl: 'transfer-form.component.html',
})
export class TransferFormComponent implements OnInit, AfterContentChecked, OnDestroy {
    @Input()
    public transaction: Partial<Transaction>
    @ViewChild('transferForm')
    public transferForm: NgForm

    public minAmount: string | number | undefined
    @Input()
    public recipient: string
    @Input()
    public lockRecipient = false
    @Input()
    public showContactBtn = true
    public files: File[] = []
    public purposes = [
        `${this.translate.instant('common.employment-income')}`,
        `${this.translate.instant('common.social-security')}`,
        `${this.translate.instant('common.trust-fund-income')}`,
        `${this.translate.instant('common.pre-funding-remittance-account')}`,
        `${this.translate.instant('transfer-form.savings')}`,
        `${this.translate.instant('transfer-form.capital-injection')}`,
        `${this.translate.instant('transfer-form.cash-disbursements')}`,
        `${this.translate.instant('common.purchase-sales-of-goods')}`,
        `${this.translate.instant('transfer-form.invoice-supplier-payment')}`,
        `${this.translate.instant('common.tax-payment')}`,
        `${this.translate.instant('common.license-subscription-fee')}`,
        `${this.translate.instant('common.payment-of-interest')}`,
        `${this.translate.instant('common.bill-utilities-payment')}`,
        `${this.translate.instant('common.insurance-payment')}`,
        `${this.translate.instant('common.refund-rebate')}`,
        `${this.translate.instant('common.commission-bonus-payment')}`,
        `${this.translate.instant('common.family-maintenance-education-medical-travel')}`,
        `${this.translate.instant('common.pre-funding-foreign-exchange-account')}`,
        `${this.translate.instant('transfer-form.loan-payment')}`,
        `${this.translate.instant('common.investment-securities')}`,
        `${this.translate.instant('common.transport')}`,
        `${this.translate.instant('common.property-payment')}`,
    ]

    public displayName = environment.displayName
    public inclusiveFee = true
    public isUploadCollapsed = true
    public faQuestionCircle = faQuestionCircle

    private fee: IFee | null = null

    private subscriptions = new Subscription()
    private fetchEvent = new Subject<void>()

    constructor(
        public session: SessionService,
        private feeService: FeeService,
        private http: HttpClient,
        private ngbModal: NgbModal,
        private confirmation: ConfirmationService,
        private walletsService: WalletsService,
        private route: ActivatedRoute,
        private walletsQuery: WalletsQuery,
        private currenciesQuery: CurrenciesQuery,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            combineLatest(this.fetchEvent, this.session.userStream)
                .pipe(
                    tap(([_, user]) => {
                        this.transaction.baseWallet = {
                            user: {
                                id: user.id,
                                accountNumber: user.accountNumber,
                                name: user.name,
                            },
                        } as Wallet
                    }),
                    switchMap(([_, user]) =>
                        this.currenciesQuery.selectEntity(user.preferredCurrency.code).pipe(
                            take(1),
                            flatMap(preferredCurrency => {
                                if (!this.transaction.currency) {
                                    this.transaction.currency = preferredCurrency
                                }
                                return this.feeService.fetch({
                                    type: 'transfer',
                                    method: 'internal',
                                    user: { id: user.id } as IUser,
                                    currency: { code: this.transaction.currency?.code } as ICurrency,
                                })
                            })
                        )
                    )
                )
                .subscribe(fee => {
                    this.fee = fee
                    Object.assign(this.transaction, this.feeService.determineFee(this.transaction, fee as any))
                    if (fee && fee.minValue) {
                        this.minAmount = Currency.convert(fee.minValue, fee.currency, this.transaction.currency!)
                    }
                })
        )
        this.fetchEvent.next()
        if (this.route.snapshot.queryParamMap.has('recipient')) {
            this.recipient = this.route.snapshot.queryParamMap.get('recipient')!
            this.onRecipientChange()
        }
    }

    public ngAfterContentChecked(): void {
        if (this.recipient) {
            this.onRecipientChange()
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.transferForm && this.transferForm.invalid) {
            return
        }
        const modal = this.ngbModal.open(PaymentConfirmationComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
        })
        const component = modal.componentInstance as PaymentConfirmationComponent
        component.transaction = this.transaction as Transaction
        component.onConfirm.subscribe(() => {
            this.createTransaction().subscribe(
                transaction => {
                    component.isCompleted = true
                    modal.close()
                    const transactionView = this.ngbModal.open(TransactionViewComponent, {
                        windowClass: 'modal-primary',
                        backdrop: 'static',
                    })
                    const transactionViewModal = transactionView.componentInstance as TransactionViewComponent
                    transactionViewModal.transaction = transaction
                    this.reset()
                    this.walletsService.fetch()
                    this.fetchEvent.next()
                },
                error => {
                    if (error.name === 'RecipientNotFoundError') {
                        modal.close()
                        this.confirmation
                            .show({
                                title: this.translate.instant('common.recipient-not-found'),
                                type: 'danger',
                                text: this.translate.instant('common.recipient-not-found-desc'),
                                confirmText: this.translate.instant('common.understood'),
                                showCancel: false,
                            })
                            .subscribe()
                    }
                    component.isSubmitted = false
                    component.reset()
                }
            )
        })
    }

    public openContactBook(): void {
        if (!this.transaction.baseWallet?.user) {
            return
        }
        const modal = openContactBook(this.ngbModal, this.transaction.baseWallet.user)
        this.subscriptions.add(
            (modal.componentInstance as ContactBookComponent).onSelect.subscribe((contact: IContact) => {
                this.recipient = contact.counterUser?.accountNumber
                this.onRecipientChange(contact)
                modal.close()
            })
        )
    }

    public onRecipientChange(contact?: IContact): void {
        this.transaction.counterWallet = {
            user: contact
                ? contact.counterUser
                : { accountNumber: !this.recipient ? '' : this.recipient.trim().replace(/\s/g, '') },
        } as Wallet
    }

    public useMaxAmount(): void {
        if (this.transaction.currency) {
            this.walletsQuery.balances$.subscribe(balances => {
                const found = balances.find(balance => balance.currency.code === this.transaction.currency!.code)
                if (found) {
                    this.transaction.amount = found.availableBalance

                    if (!this.inclusiveFee && this.fee) {
                        const maxFee = new BigNumber(
                            Currency.convert(this.fee.max!, this.fee.currency, this.transaction.currency!)
                        )
                        const relativeFeeAmount = new BigNumber(this.transaction.amount).times(
                            this.transaction.relativeFee ?? 0
                        )
                        let fee = new BigNumber(this.transaction.fixedFee || 0).plus(relativeFeeAmount)
                        if (fee.isGreaterThan(maxFee)) {
                            fee = maxFee
                        }
                        this.transaction.amount = new BigNumber(this.transaction.amount)
                            .minus(fee)
                            .toFixed(this.transaction.currency!.decimalPlaces, 1)
                    }

                    if (new BigNumber(this.transaction.amount).isLessThan(0)) {
                        this.transaction.amount = '0'
                    }

                    this.transaction.amount = parseFloat(this.transaction.amount).toString()
                }
            })
        }
    }

    public createTransaction(): Observable<Transaction> {
        const body = {
            ...this.transaction,
        }
        if (!this.inclusiveFee) {
            body.amount = undefined
            body.settledAmount = this.transaction.amount
        }
        return this.http.post<Transaction>('/transactions', body).pipe(
            flatMap(transaction =>
                this.files.length > 0
                    ? forkJoin(
                          this.files.map(file =>
                              this.http.post<File>(`/transactions/${transaction.id}/files`, file, {
                                  headers: new HttpHeaders({
                                      'X-File-Name': encodeURIComponent(file.name),
                                      'Content-Type': file.type,
                                  }),
                              })
                          )
                      ).pipe(map(() => transaction))
                    : of(transaction)
            )
        )
    }

    public toggleInclusiveFee(): void {
        this.inclusiveFee = !this.inclusiveFee
        this.transaction.settledAmount = !this.inclusiveFee ? this.transaction.amount : undefined
    }

    public onCurrencyChange(): void {
        this.fetchEvent.next()
    }

    public onAmountChange(): void {
        Object.assign(this.transaction, this.feeService.determineFee(this.transaction, this.fee as any))
    }

    public reset(): void {
        this.files = []
        this.inclusiveFee = true
        this.transferForm.resetForm({
            inclusiveFee: true,
            amount: undefined,
            purpose: undefined,
            comment: undefined,
            'currency-select': this.transaction.currency,
        })
    }
}
